import React, { ReactNode } from "react";
import { Noto_Sans, EB_Garamond } from "next/font/google";
import AccessibilityBar from "@/components/layout/AccessibilityBar";
import Header from "@/components/layout/Header";
import MainNav from "@/components/layout/MainNav";
import Footer from "@/components/layout/Footer";
import "./globals.css";

const notoSans = Noto_Sans({
  subsets: ["latin", "devanagari"],
  weight: ["400", "500", "600", "700"],
  variable: "--font-noto-sans"
});

const garamond = EB_Garamond({
  subsets: ["latin"],
  weight: ["400", "500", "600", "700"],
  variable: "--font-garamond"
});

export const metadata = {
  title: "SLCR - Smart Laboratory on Clean Rivers",
  description: "Indo-Danish initiative for innovative river rejuvenation and wastewater treatment technologies.",
  keywords: ["SLCR", "Clean Rivers", "Wastewater Treatment", "India", "Denmark", "Jal Shakti", "Namami Gange"],
};

interface RootLayoutProps {
  children: ReactNode;
}

export default function RootLayout({ children }: RootLayoutProps) {
  return (
    <html lang="en" className={`${notoSans.variable} ${garamond.variable}`}>
      <body className={notoSans.className}>
        {/* Accessibility Bar */}
        <AccessibilityBar />

        {/* Header with Logos */}
        <Header />

        {/* Main Navigation */}
        <MainNav />

        {/* Main Content */}
        <main id="main-content" tabIndex={-1} className="focus:outline-none min-h-screen">
          {children}
        </main>

        {/* Footer */}
        <Footer />
      </body>
    </html>
  );
}
